/* Close Button */
.close {
    position: absolute;
    right: 6px;
    top: 6px;
    width: 32px;
    height: 32px;
    opacity: 0.6;
    cursor: pointer;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 24px;
    width: 2px;
    background-color: #333;
  }
  .close:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }
  .close:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
  
